
import Group from 'vux/src/components/group/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapMutations } from 'vuex'
export default {
name:"pannel",
props:['item'],
components:{
Group,
Search,
XNumber,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XInput,
Group
},
data(){
return{
}
},
methods:{
numChange(id){      //点击产品数量时触发
let obj = {
id:id,
num:this.item.num
}
this.productNum(obj)
},
handleClose(){    //菜单关闭时触发

},
handleOpen(){   //菜单打开时触发

},
clickFav(id){   //取消模板时触发

},
clickDel(id){   //删除模板时触发
const _this = this
this.$vux.confirm.show({
title:"注意",
content:"确定要删除",
onCancel () {      //取消的回调

},
onConfirm () {    //确定的回调
_this.changeItem(id)
}
})
},
...mapMutations(['changeItem','productNum']),
},
mounted(){

}

}



