
import XButton from 'vux/src/components/x-button/index.vue'

import {mapMutations,mapState,mapGetters} from 'vuex'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name:'close',
components:{
XButton,
Flexbox,
FlexboxItem
},
data(){
return{
show:true
}
},
methods:{
...mapMutations(['toClose']),
close(){
const that = this
this.$vux.confirm.show({
title:"注意",
content:"确定要去结算吗？",
onCancel () {      //取消的回调

},
onConfirm () {    //确定的回调
that.toClose()
if(that.isHaveProduct){
that.$router.push({path:"/list"})

}else{
that.$vux.alert.show({
title: '注意',
content: '暂无可结算的产品!',
})
}
}
})

}

},
computed:{
...mapState(['isHaveProduct']),
...mapGetters(['allNum'])
},
mounted(){

let _this =this
_this.bus.$on('change',function(msg){
_this.show=msg;
})
}

}



